.main-div-invites {
  top: 109px;
  top: 20px;
  border-radius: 4px;
  overflow: visible;
  padding: 14px 20px;
  width: 1086px;
  margin: 20px auto;
  background: #fff;
  border-radius: 4px;
  position: relative;
  .markerSeen {
    width: 8px;
    height: 8px;
    background-color: #fe5151;
    border-radius: 50%;
    position: absolute;
    top: -3px;
    right: -3px;
  }
  .count-div {
    margin-left: 10px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #e4b05a;
    color: #fff;
    font-weight: 600;
  }

  .ToolTipText {
    font-size: 10px;
  }

  .top-wrapper-invites {
    display: flex;

    .search-input-invites {
      width: 195px;
      margin-right: 10px;
      margin-bottom: 20px;
      display: flex;

      .invitesSearch .MuiFormControl-root {
        background: #e1e5eb !important ;
        height: 34px !important;
      }

      .invitesSearch .MuiInputBase-input {
        padding: 6px 8px 4px !important;
        font-family: "Poppins", sans-serif;
        font-size: 11px;
      }

      .invitesSearch .MuiInputBase-root {
        height: 34px;
        background-color: #e1e5eb;
      }

      .invitesSearch .MuiOutlinedInput-root {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }

      .invitessearchicon {
        background-color: #e1e5eb;
        padding: 5px;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        cursor: pointer;
        padding-right: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .invitessearchicon img {
        height: 15px;
        width: 15px;
      }
    }

    //   .date-card-invites{
    //     min-width: 250px;
    //     margin-right: 10px;
    //     margin-bottom: 20px;

    //     .legend-wrapper-invites {
    //         font-family: "Poppins", sans-serif;
    //         width: 50px;
    //         font-size: 10px;
    //         margin-left: 13px;
    //       }
    //   }

    .select-status-invites {
      min-width: 155px;
      margin-right: 10px;
      margin-bottom: 20px;

      .css-g1d714-ValueContainer {
        position: unset;
        height: 100%;
      }
      .css-tlfecz-indicatorContainer {
        padding: 0;
      }
    }
  }

  .table-wrapper-invites {
    .short-iconBtn {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .MuiIconButton-root {
      padding: 0 !important;
    }
    .css-i4bv87-MuiSvgIcon-root {
      font-size: 18px;
    }

    .chat-image {
      height: 20px;
      width: 20px;
      margin-right: 5px;
      position: relative;
      cursor: pointer;
      img {
        height: 100%;
        width: 100%;
      }
    }
  }

  .whatsapp-invites {
    width: 490px;
    border-radius: 4px;
    height: 370px;
    position: fixed;
    right: 20px;
    bottom: 0;
    border: 1px solid #36363626;

    .whatsapp-invites-upper-div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 5px 20px;
      border-radius: 4px;
      border-bottom: 1px solid #36363659;

      .icon-heading-div {
        display: flex;
        align-items: center;
        font-weight: 600;

        .whatsapp-number {
          margin-left: 10px;
          font-size: 11px;
        }

        .whatsapp-icon {
          img {
            height: 20px;
            width: 20px;
          }
        }
      }
    }

    .whatsapp-invites-middle-div {
      height: 250px;

      textarea {
        height: 100%;
        width: 100%;
        border: none;
        overflow: auto;
        outline: none;
        padding: 20px;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        font-size: 11px;
        resize: none; /*remove the resize handle on the bottom right*/
      }
    }

    .whatsapp-invites-Button-div {
      padding: 20px;
      display: flex;
      justify-content: flex-end;
      background-color: #fff;

      .sendButtonWhatsapp {
        border-radius: 25px !important;
      }
    }
  }

  .email-invites {
    width: 400px;
    border-radius: 4px;
    height: 370px;
    position: fixed;
    right: 20px;
    bottom: 0;
    border: 1px solid #36363626;

    .email-invites-upper-div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #fff;
      color: #04724d;
      padding: 5px 20px;
      // border-radius: 4px;
      font-weight: 600;
      border-bottom: 1px solid #36363659;

      .icon-heading-div {
        display: flex;
        align-items: center;

        .email-number {
          margin-left: 10px;
          font-size: 11px;
        }
      }
    }

    .email-invites-middle-div {
      height: 250px;
      background: #fff;
      padding-top: 10px;

      .email-textarea {
        border-top: 1px solid #36363626;
        margin-top: 10px;
        textarea {
          height: 100%;
          width: 100%;
          border: none;
          overflow: auto;
          outline: none;
          padding: 20px;
          -webkit-box-shadow: none;
          -moz-box-shadow: none;
          box-shadow: none;
          font-size: 11px;
          resize: none; /*remove the resize handle on the bottom right*/
        }
      }

      .email-input {
        padding: 5px 20px;

        .email-subject-input .MuiInputBase-root {
          height: 34px;
          font-size: 11px;
          border-radius: 25px;
          font-family: "Poppins", sans-serif;
        }
      }

      .email-autocomplete {
        padding: 5px 20px;
        .MuiInputBase-root {
          padding: 0 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          height: auto !important;
          min-height: 34px !important;
          border: 2px solid #ddd;
          border-radius: 25px;
        }

        .MuiChip-outlined {
          border: 1px solid #333;
          color: #fff;
          background: black;
          height: 20px;
          margin: 5px;
        }
        .MuiChip-outlined .MuiChip-deleteIcon {
          color: #fff;
        }

        .MuiChip-outlined .MuiChip-deleteIcon:hover {
          color: #fff;
        }
      }
    }

    .email-invites-Button-div {
      padding: 20px;
      display: flex;
      justify-content: flex-end;
      background-color: #fff;

      .sendButtonWhatsapp {
        border-radius: 25px !important;
      }
    }
  }
}
.WhatsAppModel {
  .MuiCardContent-root {
    padding: 0;
  }
  border-radius: 4px;
  outline: none;
  background-color: white;
  width: 450px;
  height: 500px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  .AreaComment {
    position: relative;
    .headerComment {
      .harderArea {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img {
          margin-right: 10px;
          width: 30px;
        }
      }
      background-color: #04724d;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 0px;
      color: #fff;
      opacity: 1;
      margin-bottom: 10px;
      img {
        cursor: pointer;
      }
    }
    .NewCommentSection {
      height: 150px;
      bottom: -170px;
      width: calc(100% - 20px);
      margin: 0 10px 10px 10px;
      background-color: #f1f3f6;
      padding: 10px;
      position: absolute;
      border-radius: 10px;
      .commentTextArea {
        border: 1px solid #d8d8d8;
        border-radius: 4px;
        width: 100%;
        outline: none;
        font-size: 11px;
        padding: 10px 20px;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;

        resize: none; /*remove the resize handle on the bottom right*/
      }
      .sendComment {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
      }
    }
    .OldCommentSection {
      height: calc(450px - 130px - 55px);
      overflow: auto;
      font-family: "Poppins", sans-serif;
      // display: flex;
      // flex-direction: column;
      .oldcommentText {
        background: #ffffff;
        border: 1px solid #d8d8d8;
        border-radius: 5px;
        opacity: 1;
        margin: 5px 10px;
        padding: 5px 10px;
        // width: 75%;
        .userName {
          text-align: left;
          font-weight: 700;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
        .timeComment {
          text-align: left;
          font-weight: 400;
          font-size: 9px;
          letter-spacing: 0px;
          color: #363636;
          opacity: 1;
          margin: 2px 0 5px 0;
        }
        .noteComment {
          text-align: left;
          font-weight: 400;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
      .oldcommentText.left {
        // justify-content: flex-start;
        margin-right: 25%;
      }
      .oldcommentText.right {
        // justify-content: flex-end;
        margin-left: 25%;
      }
    }
  }
}
.whatsAppTemplateModal {
  .TemplateMain {
    background-color: #fff;
    outline: none;
    border-radius: 8px;
    width: 75%;
    position: absolute;
    left: calc(25% / 2);
    height: 75%;
    top: calc(25% / 2);
    // padding: 20px;

    .TemplateHeader {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      font-weight: 700;
      color: #04724d;
      padding: 20px;
      border-bottom: 1px solid #36363659;
      img {
        cursor: pointer;
      }
    }
    .TemplateBody {
      padding: 20px;
      width: 100%;
      height: calc(100% - 115px);
      display: flex;
      justify-content: space-between;
      overflow: auto;
      .TemplateContent {
        width: 74%;
        .ContentHeader {
          font-size: 14px;
          font-weight: 700;
          margin-bottom: 10px;
        }
        .MuiInputBase-input.Mui-disabled,
        .MuiOutlinedInput-inputMarginDense {
          font-size: 11px;
        }
        .ContentTitle {
          font-size: 11px;
        }
        .ContentBody {
          background-color: #eef0f296;
          border-radius: 1px;
          padding: 10px;
          height: 150px;
          overflow: auto;
          font-size: 11px;
          pre {
            margin-bottom: 0;
            word-wrap: break-word;
            white-space: break-spaces;
          }
        }
        .ContentInput {
          width: 100%;
          .LabelText {
            font-size: 12px;
            font-weight: 700;
            text-transform: capitalize;
            margin-bottom: 5px;
          }
          .input-fields {
            background-color: #eef0f296;
            outline: none;
          }
          .templateInput {
            margin-bottom: 10px;
          }
        }
        .line {
          border-bottom: 1px solid #36363659;
          margin: 15px 0;
        }
      }
      .TemplatePreview {
        width: 24%;
        padding: 10px;
        border-radius: 4px;
        background-color: #fff8dc;
        .preview-heading {
          font-weight: 700;
          margin-bottom: 10px;
        }
        .preview-card {
          background-color: #fff;
          font-size: 11px;
          padding: 20px;
          border-bottom-left-radius: 25px;
          border-top-right-radius: 25px;
          border-bottom-right-radius: 25px;
        }
        .preview-button {
          background-color: #fff;
          font-size: 12px;
          padding: 10px;
          border-radius: 4px;
          margin: 10px 0;
          color: #04724d;
          font-weight: 700;
          text-align: center;
          cursor: pointer;
        }
      }
    }
    .TemplateFooter {
      height: 50px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      border-top: 1px solid #36363659;
      .sendButtonTemplate {
        margin-right: 20px !important;
        margin-bottom: 0 !important;
      }
    }
  }
}
