.HistoryPageMain {
  top: 10px;
  margin: 20px;
  border-radius: 4px;
  overflow: visible;
  padding: 14px 20px;
  width: 1180px;
  margin: 20px auto;
  background: #fff;
  border-radius: 4px;
  // position: relative;

  .BackButtonHeader {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    // border-bottom: 1px solid #ddd;
    // margin: 20px 0 0 30px;
    .arrow-div {
      background: #21745d1c;
      padding: 5px 10px;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      margin-right: 10px;
      cursor: pointer;
    }
    .score-details-heading {
      font-size: 16px;
      font-weight: 700;
    }
  }
  .TimelineFilter {
    .filter-timeline {
      width: 200px;
      border-radius: 4px !important;
    }
  }
  .ContentArea {
    width: 100%;
    display: flex;
    justify-content: space-between;
    max-height: 480px;
    position: relative;

    .InfoArea {
      width: calc(40% - 10px);
      border: 1px solid #bcbcbc;
      border-radius: 13px;
      padding: 25px;
      .TopInfo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 5px;
        .NameSection {
          text-align: left;
          letter-spacing: 0px;
          color: #000000;
          font-size: 20px;
          font-weight: 700;
          width: 70%;
        }
        .ActionSection {
          width: 30%;
          display: flex;
          justify-content: flex-end;
          img {
            width: 34px;
            height: 34px;
            margin-left: 10px;
            cursor: pointer;
          }
        }
      }
      .SubSection {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 11px;
        font-weight: 600;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
        margin: 5px 0;
        .CountAppear {
          background-color: #e4b05a;
          border-radius: 50%;
          width: 25px;
          height: 25px;
          margin-left: 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #fff;
        }
        .countText {
          text-align: center;
          font-size: 11px;
          font-weight: 500;
          letter-spacing: 0px;
          color: #000000;
          width: 200px;
        }
      }
      .Details {
        margin: 30px 0 0 0;
        .DetailRow {
          display: flex;
          margin-bottom: 20px;
          .Title {
            width: 30%;
            text-align: left;
            font-size: 13px;
            font-weight: 700;
            letter-spacing: 0px;
            color: #000000;
            opacity: 1;
          }
          .colon {
            width: 5%;
            text-align: left;
            font-size: 13px;
            font-weight: 700;
            letter-spacing: 0px;
            color: #000000;
            opacity: 1;
          }
          .value {
            width: 65%;
            text-align: left;
            letter-spacing: 0px;
            font-size: 11px;
            font-weight: 500;
            color: #000000;
            opacity: 1;
          }
        }
      }
    }
    .Timeline {
      width: calc(60% - 10px);
      border: 1px solid #bcbcbc;
      border-radius: 13px;
      padding: 50px 70px 50px 50px;
      overflow: auto;
      .TimelineNOData {
        font-size: 12px;
        font-weight: 700;
        text-align: center;
      }
      .BubbleMain {
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;
        margin-bottom: 40px;
        .DateDiv {
          background: #ffffff;
          border: 1px solid #7070704d;
          border-radius: 11px;
          opacity: 1;
          text-align: left;
          font-size: 12px;
          font-weight: 700;
          letter-spacing: 0px;
          color: #000000;
          padding: 15px 30px;
          position: relative;
          max-height: 50px;
          margin-top: 0px;
          span {
            position: absolute;
            width: 14px;
            height: 14px;
            background-color: #04724d;
            border-radius: 50%;
            right: -7px;
          }
        }
        .Count {
          margin: 0px 15px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          .line {
            border-left: 1px solid #707070;
            opacity: 0.35;
          }
          .number {
            margin-top: 4px;
            width: 35px;
            height: 35px;
            background-color: #04724d;
            border-radius: 50%;
            color: #fff;
            font-weight: 900;
            font-size: 11px;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
        .BubleContent {
          border: 1px solid #7070704d;
          border-radius: 11px;
          opacity: 1;
          position: relative;
          padding: 15px 25px 0;
          width: calc(75% - 70px);
          span.bubbleContentSpan {
            position: absolute;
            width: 14px;
            height: 14px;
            background-color: #04724d;
            border-radius: 50%;
            left: -7px;
          }
          .contents {
            display: flex;
            justify-content: space-between;
            margin-bottom: 15px;
            .text {
              .infoText {
                text-align: center;
                font-size: 11px;
                letter-spacing: 0px;
                color: #000000;
                opacity: 0.73;
                font-weight: 500;
              }
              .infoSpan {
                background-color: #e4b05a;
                padding: 5px;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 10px;
                cursor: pointer;
                .infoIcon {
                  border-radius: 50%;
                  background-color: #fff;
                  color: #000000;
                  font-size: 8px;
                  padding: 0px 5px;
                }
              }
              text-align: left;
              font-size: 12px;
              font-weight: 700;
              letter-spacing: 0px;
              color: #000000;
              opacity: 1;
              display: flex;
              margin-right: 10px;
              img {
                width: 22px;
                height: 22px;
                margin-right: 15px;
              }
            }
            .time {
              text-align: left;
              font-size: 11px;
              font-weight: 500;
              letter-spacing: 0px;
              color: #000000;
              opacity: 0.51;
              width: 60px;
            }
          }
        }
      }
    }
  }
}
.whatsapp-invites {
  width: 490px;
  border-radius: 4px;
  height: 370px;
  position: absolute;
  right: 0;
  bottom: 0;
  border: 1px solid #36363626;

  .whatsapp-invites-upper-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
    border-radius: 4px;
    border-bottom: 1px solid #36363659;

    .icon-heading-div {
      display: flex;
      align-items: center;
      font-weight: 600;

      .whatsapp-number {
        margin-left: 10px;
        font-size: 11px;
      }

      .whatsapp-icon {
        img {
          height: 20px;
          width: 20px;
        }
      }
    }
  }

  .whatsapp-invites-middle-div {
    height: 250px;

    textarea {
      height: 100%;
      width: 100%;
      border: none;
      overflow: auto;
      outline: none;
      padding: 20px;
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
      font-size: 11px;
      resize: none; /*remove the resize handle on the bottom right*/
    }
  }

  .whatsapp-invites-Button-div {
    padding: 20px;
    display: flex;
    justify-content: flex-end;
    background-color: #fff;

    .sendButtonWhatsapp {
      border-radius: 25px !important;
    }
  }
}

.email-invites {
  width: 400px;
  border-radius: 4px;
  height: 370px;
  position: absolute;
  right: 0;
  bottom: 0;
  border: 1px solid #36363626;

  .email-invites-upper-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    color: #04724d;
    padding: 5px 20px;
    // border-radius: 4px;
    font-weight: 600;
    border-bottom: 1px solid #36363659;

    .icon-heading-div {
      display: flex;
      align-items: center;

      .email-number {
        margin-left: 10px;
        font-size: 11px;
      }
    }
  }

  .email-invites-middle-div {
    height: 250px;
    background: #fff;
    padding-top: 10px;

    .email-textarea {
      border-top: 1px solid #36363626;
      margin-top: 10px;
      textarea {
        height: 100%;
        width: 100%;
        border: none;
        overflow: auto;
        outline: none;
        padding: 20px;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        font-size: 11px;
        resize: none; /*remove the resize handle on the bottom right*/
      }
    }

    .email-input {
      padding: 5px 20px;

      .email-subject-input .MuiInputBase-root {
        height: 34px;
        font-size: 11px;
        border-radius: 25px;
      }
    }

    .email-autocomplete {
      padding: 5px 20px;
      .MuiInputBase-root {
        padding: 0 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto !important;
        min-height: 34px !important;
        border: 2px solid #ddd;
        border-radius: 25px;
      }

      .MuiChip-outlined {
        border: 1px solid #333;
        color: #fff;
        background: black;
        height: 20px;
        margin: 5px;
      }
      .MuiChip-outlined .MuiChip-deleteIcon {
        color: #fff;
      }

      .MuiChip-outlined .MuiChip-deleteIcon:hover {
        color: #fff;
      }
    }
  }

  .email-invites-Button-div {
    padding: 20px;
    display: flex;
    justify-content: flex-end;
    background-color: #fff;

    .sendButtonWhatsapp {
      border-radius: 25px !important;
    }
  }
}
.WhatsAppModel {
  .MuiCardContent-root {
    padding: 0;
  }
  border-radius: 4px;
  outline: none;
  background-color: white;
  width: 450px;
  height: 500px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  .AreaComment {
    position: relative;
    .headerComment {
      .harderArea {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img {
          margin-right: 10px;
          width: 30px;
        }
      }
      background-color: #04724d;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 0px;
      color: #fff;
      opacity: 1;
      margin-bottom: 10px;
      img {
        cursor: pointer;
      }
    }
    .NewCommentSection {
      height: 150px;
      bottom: -170px;
      width: calc(100% - 20px);
      margin: 0 10px 10px 10px;
      background-color: #f1f3f6;
      padding: 10px;
      position: absolute;
      border-radius: 10px;
      .commentTextArea {
        border: 1px solid #d8d8d8;
        border-radius: 4px;
        width: 100%;
        outline: none;
        font-size: 11px;
        padding: 10px 20px;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;

        resize: none; /*remove the resize handle on the bottom right*/
      }
      .sendComment {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
      }
    }
    .OldCommentSection {
      height: calc(450px - 130px - 55px);
      overflow: auto;
      font-family: "Poppins", sans-serif;
      // display: flex;
      // flex-direction: column;
      .oldcommentText {
        background: #ffffff;
        border: 1px solid #d8d8d8;
        border-radius: 5px;
        opacity: 1;
        margin: 5px 10px;
        padding: 5px 10px;
        // width: 75%;
        .userName {
          text-align: left;
          font-weight: 700;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
        .timeComment {
          text-align: left;
          font-weight: 400;
          font-size: 9px;
          letter-spacing: 0px;
          color: #363636;
          opacity: 1;
          margin: 2px 0 5px 0;
        }
        .noteComment {
          text-align: left;
          font-weight: 400;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
      .oldcommentText.left {
        // justify-content: flex-start;
        margin-right: 25%;
      }
      .oldcommentText.right {
        // justify-content: flex-end;
        margin-left: 25%;
      }
    }
  }
}
